<template>
    <v-row class="cus_card">
        <v-col cols="6" class="col_padding">
            <draggable class="list-group" :list="list1" group="people" @start="drag=true" @end="drag=true">
                <transition-group>
                    <v-list-item-group v-for="element in list1" :key="element.name">
                        <v-list-item-title> {{ element.name }}</v-list-item-title>
                        <!-- {{ element.info }} -->
                        <v-img :src="element.img_photo" />
                        <div class="task">
                            {{ element.task }}
                        </div>
                    </v-list-item-group>
                </transition-group>
            </draggable>
        </v-col>
        <v-col cols="6" class="col_padding">
            <draggable class="list-group" :list="list2" group="people" @start="drag=true" @end="drag=true">
                <transition-group>
                    <v-list-item-group v-for="element in list2" :key="element.name">
                        <v-list-item-title> {{ element.name }}</v-list-item-title>
                        <!-- {{ element.info }} -->
                        <v-img :src="element.img_photo" />
                        <div class="task">
                            {{ element.task }}
                        </div>
                    </v-list-item-group>
                </transition-group>
            </draggable>
        </v-col>
    </v-row>
</template>

<script>
export default {
    name: "HelloWorld",
    data() {
        return {
            msg: "Welcome to Your Vue.js App"
        };
    }
};
</script>

